import {ajax} from '../../common/js/util.js'
import jweixin from '../../common/js/wx.js'
import MescrollUni from "../../components/mescroll-uni/mescroll-uni.vue"
export default {
	data() {
		return {
			Color: '#DE2910',
			value: '',
			backColor: '#FFFFFF',
			show: false,
			show1: false,
			listData: {},
			listItem: [],
			mescroll: null, //mescroll实例对象
			downOption: {
				use: true, // 是否启用下拉刷新; 默认true
				auto: true, // 是否在初始化完毕之后自动执行下拉刷新的回调; 默认true
				autoShowLoading: true, // 如果设置auto=true(在初始化完毕之后自动执行下拉刷新的回调),那么是否显示下拉刷新的进度; 默认false
				isLock: false, // 是否锁定下拉刷新,默认false;
				isBoth: true, // 下拉刷新时,如果滑动到列表底部是否可以同时触发上拉加载;默认true,两者可同时触发;
				offset: 80, // 在列表顶部,下拉大于80upx,松手即可触发下拉刷新的回调
				inOffsetRate: 1, // 在列表顶部,下拉的距离小于offset时,改变下拉区域高度比例;值小于1且越接近0,高度变化越小,表现为越往下越难拉
				outOffsetRate: 0.2, // 在列表顶部,下拉的距离大于offset时,改变下拉区域高度比例;值小于1且越接近0,高度变化越小,表现为越往下越难拉
				bottomOffset: 20, // 当手指touchmove位置在距离body底部20upx范围内的时候结束上拉刷新,避免Webview嵌套导致touchend事件不执行
				minAngle: 45, // 向下滑动最少偏移的角度,取值区间  [0,90];默认45度,即向下滑动的角度大于45度则触发下拉;而小于45度,将不触发下拉,避免与左右滑动的轮播等组件冲突;
				textInOffset: '下拉刷新', // 下拉的距离在offset范围内的提示文本
				textOutOffset: '释放更新', // 下拉的距离大于offset范围的提示文本
				textLoading: '加载中 ...' // 加载中的提示文本
			},
			upOption: {
				use: true, // 是否启用上拉加载; 默认true
				auto: true, // 是否在初始化完毕之后自动执行上拉加载的回调; 默认true
				isLock: false, // 是否锁定上拉加载,默认false;
				isBoth: true, // 上拉加载时,如果滑动到列表顶部是否可以同时触发下拉刷新;默认true,两者可同时触发;
				page: {
					num: 0, // 当前页码,默认0,回调之前会加1,即callback(page)会从1开始
					size: 10, // 每页数据的数量
					time: null // 加载第一页数据服务器返回的时间; 防止用户翻页时,后台新增了数据从而导致下一页数据重复;
				},
				noMoreSize: 6, // 如果列表已无数据,可设置列表的总数量要大于等于5条才显示无更多数据;避免列表数据过少(比如只有一条数据),显示无更多数据会不好看
				textLoading: '加载中 ...', // 加载中的提示文本
				textNoMore: '-- 没有更多数据 --', // 没有更多数据的提示文本
				toTop: {
					// 回到顶部按钮,需配置src才显示
					src: "http://www.mescroll.com/img/mescroll-totop.png", // 图片路径
					offset: 1000, // 列表滚动多少距离才显示回到顶部按钮,默认1000
					duration: 300 // 回到顶部的动画时长,默认300ms
				},
				empty: {
					use: true, // 是否显示空布局
					icon: "http://www.mescroll.com/img/mescroll-empty.png", // 图标路径
					tip: '暂无相关数据', // 提示
					// btnText: '去逛逛 >' // 按钮
				}
			}
		}
	},
	components: {
		MescrollUni
	},
	onUnload() {
		this.mescroll = null;
	},
	//注册滚动到底部的事件,用于上拉加载
	onReachBottom() {
		this.mescroll && this.mescroll.onReachBottom()
	},
	//注册列表滚动事件,用于下拉刷新
	onPageScroll(e) {
		this.mescroll && this.mescroll.onPageScroll(e)
	},
	onLoad() {
		//刷新保留用户uid
		if (this.getRequest('uid')) {
			var uids = this.getRequest('uid')
			this.$store.commit('changeUid', uids)
		}
	},
	onShow() {
		let self = this
		//分享给朋友
		let url = location.href.split('#')[0]
		let datas = {
			url: url
		}
		let data = {
			url: '/api/gzh/auth',
			data: datas,
			success: function(res){
				if(res.data.result == 0){
					console.log(res)
					var appId = res.data.appId
					var timestamp = res.data.timestamp
					var nonceStr = res.data.noncestr
					var signature = res.data.signature
					self.shareFriend(appId, timestamp, nonceStr, signature)
				}
			}
		}
		ajax(data)
		
		//分享到朋友圈
		let circle = {
			url: '/api/gzh/auth',
			data: datas,
			success: function(res){
				if(res.data.result == 0){
					console.log(res)
					var appId = res.data.appId
					var timestamp = res.data.timestamp
					var nonceStr = res.data.noncestr
					var signature = res.data.signature
					self.shareMenu(appId, timestamp, nonceStr, signature)
				}
			}
		}
		ajax(circle)
	},
	methods: {
		getRequest(variable) {
			var query = window.location.search.substring(1);
			var vars = query.split("&");
			for (var i = 0; i < vars.length; i++) {
				var pair = vars[i].split("=");
				if (pair[0] == variable) {
					return pair[1];
				}
			}
			return (false);
		},
		share() {
			this.show = true
		},
		hideShare() {
			this.show = false
		},
		shareFriend(appId, timestamp, nonceStr, signature) {
			let self = this
			jweixin.config({
				// 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
				debug: false,
				// 必填，公众号的唯一标识
				appId: appId,
				// 必填，生成签名的时间戳
				timestamp: timestamp,
				// 必填，生成签名的随机串
				nonceStr: nonceStr,
				// 必填，签名，见附录1
				signature: signature,
				// 必填，需要使用的JS接口列表，所有JS接口列表见附录2
				jsApiList: ['checkJsApi', 'onMenuShareTimeline', 'onMenuShareAppMessage']
			});
			jweixin.error(function(res) {
				//这个地方的好处就是wx.config配置错误，会弹出窗口哪里错误，然后根据微信文档查询即可。
				// alert("扫码出错了1：" + res.errMsg)
			});
			jweixin.ready(function() {
				jweixin.onMenuShareAppMessage({
					title: '小岗村', // 分享标题
					desc: '小岗村欢迎您的加入', // 分享描述
					link: 'http://m.xgcyz1978.com/api/gzh/wxLogin?inviteId=' + self.$store.state.uid,
					imgUrl: 'http://m.xgcyz1978.com/h5/static/img/logo1.png', // 分享图标
					type: 'link', // 分享类型,music、video或link，不填默认为link
					dataUrl: '', // 如果type是music或video，则要提供数据链接，默认为空
					success: function() {
						self.show = false
						Toast('分享成功')
						// 用户确认分享后执行的回调函数
					},
					cancel: function() {
						self.show = false
						Toast('分享失败')
						// 用户取消分享后执行的回调函数
					}
				})
			})
		},
		shareMenu(appId, timestamp, nonceStr, signature) {
			let self = this
			jweixin.config({
				// 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
				debug: false,
				// 必填，公众号的唯一标识
				appId: appId,
				// 必填，生成签名的时间戳
				timestamp: timestamp,
				// 必填，生成签名的随机串
				nonceStr: nonceStr,
				// 必填，签名，见附录1
				signature: signature,
				// 必填，需要使用的JS接口列表，所有JS接口列表见附录2
				jsApiList: ['checkJsApi', 'onMenuShareTimeline', 'onMenuShareAppMessage']
			});
			jweixin.error(function(res) {
				//这个地方的好处就是wx.config配置错误，会弹出窗口哪里错误，然后根据微信文档查询即可。
				// alert("扫码出错了1：" + res.errMsg)
			});
			jweixin.ready(function() {
				jweixin.onMenuShareTimeline({
					title: '小岗村', // 分享标题
					link: 'http://m.xgcyz1978.com/api/gzh/wxLogin?inviteId=' + self.$store.state.uid,
					imgUrl: 'http://m.xgcyz1978.com/h5/static/img/logo1.png', // 分享图标
					success: function() {
						self.show = false
						Toast('分享成功')
						// 用户确认分享后执行的回调函数
					},
					cancel: function() {
						self.show = false
						Toast('分享失败')
						// 用户取消分享后执行的回调函数
					}
				})
			})
		},
		// mescroll组件初始化的回调,可获取到mescroll对象
		mescrollInit(mescroll) {
			this.mescroll = mescroll
		},
		/*下拉刷新的回调 */
		downCallback(mescroll) {
			// 这里加载你想下拉刷新的数据, 比如刷新轮播数据
			// loadSwiper();
			// 下拉刷新的回调,默认重置上拉加载列表为第一页 (自动执行 mescroll.num=1, 再触发upCallback方法 )
			this.listItem = []
			mescroll.resetUpScroll()
		},
		/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
		upCallback(mescroll) {
			let pageNum = mescroll.num // 页码, 默认从1开始
			let pageSize = mescroll.size // 页长, 默认每页10条
			let self = this
			//好友邀请列表
			let lists = {
				uid: this.$store.state.uid
			}
			let list = {
				url: '/api/gzh/inviteList',
				data: lists,
				success: function(res){
					if(res.data.result == 0){
						console.log(res)
						self.listData = res.data
						for (var j = 0; j < res.data.dataList.length; j++) {
							self.listItem.push(res.data.dataList[j])
						}
					}
					// 接口返回的当前页数据列表 (数组)
					let curPageData = res.data.dataList
					// 接口返回的总页数 (比如列表有26个数据,每页10条,共3页; 则totalPage值为3)
					let totalPage = res.data.totalPage
					mescroll.endByPage(curPageData.length, totalPage)
				}
			}
			ajax(list)
		},
		// 点击回到顶部按钮的回调
		topClick() {
			console.log('点击了回到顶部按钮');
		}
	}
}
